html, body {
  margin: 0;
  padding: 0;
}

button {
  vertical-align: baseline;
  font-family: inherit;
  font-size: 100%;
  font-weight: inherit;
  color: inherit;
  appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
}

body {
  color: #111;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5f5;
  min-width: 230px;
  max-width: 600px;
  margin: 0 auto;
  font: 300 14px / 1.4em Helvetica Neue, Helvetica, Arial, sans-serif;
}

:focus {
  outline: 0;
}

.hidden {
  display: none;
}

.todoapp {
  background: #fff;
  margin: 130px 0 40px;
  position: relative;
  box-shadow: 0 2px 4px #0003, 0 25px 50px #0000001a;
}

.todoapp input::-webkit-input-placeholder {
  color: #0006;
  font-style: italic;
  font-weight: 300;
}

.todoapp input::placeholder {
  color: #0006;
  font-style: italic;
  font-weight: 300;
}

.todoapp input::input-placeholder {
  color: #0006;
  font-style: italic;
  font-weight: 300;
}

.todoapp h1 {
  text-align: center;
  color: #b83f45;
  -webkit-text-rendering: optimizeLegibility;
  -moz-text-rendering: optimizeLegibility;
  text-rendering: optimizelegibility;
  width: 100%;
  font-size: 80px;
  font-weight: 200;
  position: absolute;
  top: -140px;
}

.new-todo, .edit {
  font-family: inherit;
  font-size: 24px;
  font-weight: inherit;
  color: inherit;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border: 1px solid #999;
  width: 100%;
  margin: 0;
  padding: 6px;
  line-height: 1.4em;
  position: relative;
  box-shadow: inset 0 -1px 5px #0003;
}

.new-todo {
  background: #00000001;
  border: none;
  padding: 16px 16px 16px 60px;
  box-shadow: inset 0 -2px 1px #00000008;
}

.main {
  z-index: 2;
  border-top: 1px solid #e6e6e6;
  position: relative;
}

.toggle-all {
  opacity: 0;
  border: none;
  width: 1px;
  height: 1px;
  position: absolute;
  bottom: 100%;
  right: 100%;
}

.toggle-all + label {
  width: 60px;
  height: 34px;
  font-size: 0;
  position: absolute;
  top: -52px;
  left: -13px;
  transform: rotate(90deg);
}

.toggle-all + label:before {
  content: "❯";
  color: #e6e6e6;
  padding: 10px 27px;
  font-size: 22px;
}

.toggle-all:checked + label:before {
  color: #737373;
}

.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.todo-list li {
  border-bottom: 1px solid #ededed;
  font-size: 24px;
  position: relative;
}

.todo-list li:last-child {
  border-bottom: none;
}

.todo-list li.editing {
  border-bottom: none;
  padding: 0;
}

.todo-list li.editing .edit {
  width: calc(100% - 43px);
  margin: 0 0 0 43px;
  padding: 12px 16px;
  display: block;
}

.todo-list li.editing .view {
  display: none;
}

.todo-list li .toggle {
  text-align: center;
  appearance: none;
  opacity: 0;
  border: none;
  width: 40px;
  height: auto;
  margin: auto 0;
  position: absolute;
  top: 0;
  bottom: 0;
}

.todo-list li .toggle + label {
  background-image: url("data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23ededed%22%20stroke-width%3D%223%22/%3E%3C/svg%3E");
  background-position: 0;
  background-repeat: no-repeat;
}

.todo-list li .toggle:checked + label {
  background-image: url("data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23bddad5%22%20stroke-width%3D%223%22/%3E%3Cpath%20fill%3D%22%235dc2af%22%20d%3D%22M72%2025L42%2071%2027%2056l-4%204%2020%2020%2034-52z%22/%3E%3C/svg%3E");
}

.todo-list li label {
  word-break: break-all;
  color: #4d4d4d;
  padding: 15px 15px 15px 60px;
  font-weight: 400;
  line-height: 1.2;
  transition: color .4s;
  display: block;
}

.todo-list li.completed label {
  color: #cdcdcd;
  text-decoration: line-through;
}

.todo-list li .destroy {
  color: #cc9a9a;
  width: 40px;
  height: 40px;
  margin: auto 0 11px;
  font-size: 30px;
  transition: color .2s ease-out;
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10px;
}

.todo-list li .destroy:hover {
  color: #af5b5e;
}

.todo-list li .destroy:after {
  content: "×";
}

.todo-list li:hover .destroy {
  display: block;
}

.todo-list li .edit {
  display: none;
}

.todo-list li.editing:last-child {
  margin-bottom: -1px;
}

.footer {
  text-align: center;
  border-top: 1px solid #e6e6e6;
  height: 20px;
  padding: 10px 15px;
  font-size: 15px;
}

.footer:before {
  content: "";
  height: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  box-shadow: 0 1px 1px #0003, 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px #0003, 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px #0003;
}

.todo-count {
  float: left;
  text-align: left;
}

.todo-count strong {
  color: #059790;
  font-weight: 700;
}

.filters {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  left: 0;
  right: 0;
}

.filters li {
  display: inline;
}

.filters li a {
  color: inherit;
  border: 1px solid #0000;
  border-radius: 3px;
  margin: 3px;
  padding: 3px 7px;
  text-decoration: none;
}

.filters li a:hover {
  border-color: #af2f2f1a;
}

.filters li a.selected {
  border-color: #af2f2f33;
}

.clear-completed, html .clear-completed:active {
  float: right;
  cursor: pointer;
  line-height: 20px;
  text-decoration: none;
  position: relative;
}

.clear-completed:hover {
  text-decoration: underline;
}

.info {
  color: #4d4d4d;
  text-shadow: 0 1px #ffffff80;
  text-align: center;
  margin: 65px auto 0;
  font-size: 11px;
}

.info p {
  line-height: 1;
}

.info a {
  color: inherit;
  font-weight: 400;
  text-decoration: none;
}

.info a:hover {
  text-decoration: underline;
}

@media screen and (-webkit-device-pixel-ratio >= 0) {
  .toggle-all, .todo-list li .toggle {
    background: none;
  }

  .todo-list li .toggle {
    height: 40px;
  }
}

@media (width <= 430px) {
  .footer {
    height: 50px;
  }

  .filters {
    bottom: 10px;
  }
}

hr {
  border: 0;
  border-top: 1px dashed #c5c5c5;
  border-bottom: 1px dashed #f7f7f7;
  margin: 20px 0;
}

.learn a {
  color: #b83f45;
  font-weight: normal;
  text-decoration: none;
}

.learn a:hover {
  color: #787e7e;
  text-decoration: underline;
}

.learn h3, .learn h4, .learn h5 {
  color: #000;
  margin: 10px 0;
  font-weight: 500;
  line-height: 1.2;
}

.learn h3 {
  font-size: 24px;
