}

.learn p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 15px;
  font-weight: 300;
  line-height: 1.3;
}

#issue-count {
  display: none;
}

.quote {
  border: none;
  margin: 20px 0 60px;
}

.quote p {
  font-style: italic;
}

.quote p:before {
  content: "“";
  opacity: .15;
  font-size: 50px;
  position: absolute;
  top: -20px;
  left: 3px;
}

.quote p:after {
  content: "”";
  opacity: .15;
  font-size: 50px;
  position: absolute;
  bottom: -42px;
  right: 3px;
}

.quote footer {
  position: absolute;
  bottom: -40px;
